
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.flip-list-move[data-v-2d4acdac] {
  -webkit-transition: -webkit-transform 0.2s;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}
.todo-ul[data-v-2d4acdac] {
  width: 50%;
  max-width: 480px;
  padding-left: 0;
  margin-left: auto;
  margin-right: auto;
  font-size: 24px;
  list-style-type: none;
}
.todo-li[data-v-2d4acdac] {
  position: relative;
  text-align: left;
  border-bottom: 1px solid #cfd8dc;
}
.todo-title[data-v-2d4acdac] {
  width: 100%;
  padding: 10px 0;
  font-size: 24px;
  border: 1px solid transparent;
  outline: none;
}
.status[data-v-2d4acdac],
.delete[data-v-2d4acdac],
.priority[data-v-2d4acdac] {
  font-size: 24px;
  cursor: pointer;
}
.delete[data-v-2d4acdac] {
  color: #ef9a9a;
}
.priority.high[data-v-2d4acdac] {
  color: #7e57c2;
}
.priority.medium[data-v-2d4acdac] {
  color: #f9a825;
}
.priority.low[data-v-2d4acdac] {
  color: #00c853;
}
.status[data-v-2d4acdac] {
  position: absolute;
  left: -30px;
  top: 13px;
}
.todo-li.finished .todo-title[data-v-2d4acdac] {
  color: #cfd8dc;
}
.actions[data-v-2d4acdac] {
  position: absolute;
  top: -10px;
  right: -60px;
  padding: 20px 10px;
  opacity: 0.15;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.todo-li:hover .actions[data-v-2d4acdac],
.actions[data-v-2d4acdac]:hover {
  -webkit-transition: none;
  transition: none;
  opacity: 1;
}
@media screen and (max-width: 600px) {
.todo-ul[data-v-2d4acdac] {
    max-width: 400px;
}
}
@media screen and (max-width: 480px) {
.todo-ul[data-v-2d4acdac] {
    max-width: 320px;
}
}





















